import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/transaction/actions';

export default function OpenPendingButton({ hs = null }) {
	const dispatch = useDispatch();
	const history = useHistory();

	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { cashier = {} } = app;
	const isOpeningShift = cashier?.isOpening || false;

	const getHoldTrx = () => {
		dispatch({
			type: actions.TRANSACTION_HOLD_LIST,
			history
		});
	};

	const openModal = () => {
		hs('modalPendingPayment', true);
		getHoldTrx();
	};

	const onKeyDownGlobal = (e) => {
		if (isOpeningShift) {
			if (e.key === 'F7') {
				openModal();
			}
			return;
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);
		getHoldTrx();

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	return (
		<>
			<Button disabled={!isOpeningShift} className="btn-page" onClick={openModal}>
				<kbd className="kbd">f7</kbd>
				<p>daftar pending</p>
			</Button>
		</>
	);
}
