import React from 'react';
import { useSelector } from 'react-redux';
import ChangeQtyItem from '../widget/ChangeQtyItem';
import CheckItemPriceButton from '../widget/CheckItemPrice';
import DeleteItemButton from '../widget/DeleteItemButton';
import DiscountButton from '../widget/DiscountButton';
import ListItemButton from '../widget/ListItemButton';
import NewOrderButton from '../widget/NewOrderButton';
import OpeningShiftButton from '../widget/OpeningShiftButton';
import OpenPendingButton from '../widget/OpenPendingButton';
import OptionsButton from '../widget/OptionButton';
import PaymentButton from '../widget/PaymentButton';
import SavePendingButton from '../widget/SavePendingButton';

export default function ButtonActionOrder({ handleState = (state, val) => {}, ...props }) {
	const { selectedRowTransaction = {} } = useSelector((state) => state?.transaction);
	const { cashier = {} } = JSON.parse(localStorage.getItem('app') || '{}');
	const { isOpening = false } = cashier;

	const isSelectedRow = Object.keys(selectedRowTransaction)?.length !== 0;

	return (
		<div className="w-full h-full">
			{isOpening ? (
				isSelectedRow ? (
					<div className="w-full h-full grid grid-cols-1 gap-1" style={{ gridTemplateRows: 'repeat(9, minmax(0, 1fr))' }}>
						<div className="">
							<DeleteItemButton hs={handleState} />
						</div>
						<div className="">
							<ChangeQtyItem hs={handleState} />
						</div>
						<div className="">
							<DiscountButton />
						</div>
						{/* <div className="">
						<TabBarCodeButton />
					</div> */}
					</div>
				) : (
					<div className="w-full h-full grid grid-cols-1 gap-1" style={{ gridTemplateRows: 'repeat(9, minmax(0, 1fr))' }}>
						<div className="">
							<NewOrderButton hs={handleState} />
						</div>
						<div className="">
							<ListItemButton hs={handleState} />
						</div>
						{/* <div className="">
						<SelectItemBtn />
					</div> */}
						<div className="">
							<SavePendingButton />
						</div>
						<div className="">
							<OpenPendingButton hs={handleState} />
						</div>
						<div className="">
							<CheckItemPriceButton />
						</div>
						<div className="">
							<OptionsButton />
						</div>
						<div className="">
							<PaymentButton />
						</div>
					</div>
				)
			) : (
				<div className="w-full h-full grid grid-cols-1 gap-1" style={{ gridTemplateRows: 'repeat(9, minmax(0, 1fr))' }}>
					<div className="">
						<OpeningShiftButton />
					</div>
					<div className="">
						<OptionsButton />
					</div>
				</div>
			)}
		</div>
	);
}
