import { Form, Input, notification } from 'antd';
import React, { useEffect, useState } from 'react';
import { BiSearch } from 'react-icons/bi';
import { CgSpinner } from 'react-icons/cg';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import actions from '../../redux/transaction/actions';
import { currencyFormat } from '../../utils/helper/utilities';
import './app.css';
import ButtonActionOrder from './components/ButtonActionOrder';
import HeaderLayout from './components/HeaderLayout';
import ModalChangeQty from './components/ModalChangeQty';
import ModalConfirmDelete from './components/ModalConfirmDelete';
import ModalPendingPayment from './components/ModalPendingPayment';
import ModalTableSearchItem from './components/ModalTableSearchItem';
import TableOrderItem from './components/TableOrderItem';
import useCartOrder from './hooks/useCartOrder';
import usePayment from './hooks/usePayment';

export default function NewLayout() {
	const dispatch = useDispatch();
	const history = useHistory();

	const { getTotalQty, getGrandTotal } = usePayment();
	const { handleFocus, inputRef, form, handleSearch, handleAddItem, handleResetForm } = useCartOrder();
	const { contentStatus = 'isMenu', searchResult = [], loading = false, billNo = '', billDate = '' } = useSelector((state) => state?.transaction);
	const { isLog = false, authorized = false } = JSON.parse(localStorage.getItem('app') || '{}');

	const [state, setState] = useState({
		modalPendingPayment: false,
		modalSearchItem: false,
		modalConfirmDelete: false,
		modalQty: false,
		barcode: ''
	});

	const handleCancel = () => {
		handleFocus();

		dispatch({
			type: 'transaction/SET_STATE',
			payload: {
				contentStatus: 'isMenu'
			}
		});

		setState((prev) => ({
			...prev,
			modalPendingPayment: false,
			modalSearchItem: false,
			modalConfirmDelete: false,
			modalQty: false,
			barcode: ''
		}));
	};

	const onKeyDownGlobal = (event) => {
		if (event.key === 'Escape') {
			event.preventDefault();
			event.target.blur();
			handleFocus();
		}
	};

	const handleSearchItem = (query = '') => {
		if (query && query.length !== 0) {
			if (query.length >= 6) {
				handleSearch(query);
			} else {
				handleResetForm();

				notification.warning({
					message: 'Perhatian!',
					description: 'Masukkan kode yang benar! Min. kode adalah 6 digit'
				});

				handleFocus();
			}

			return;
		}

		handleResetForm();
		notification.warning({
			message: 'Perhatian!',
			description: 'Masukkan kode yang terlebih dahulu.'
		});
	};

	const handleState = (state, value) => {
		setState((prev) => ({
			...prev,
			[state]: value
		}));
	};

	useEffect(() => {
		handleCancel();
		handleFocus();

		if (isLog && authorized) {
			dispatch({
				type: 'transaction/BACK_MENU'
			});

			dispatch({
				type: actions.TRANSACTION_BILL_NO
			});

			dispatch({
				type: 'auth/property'
			});

			dispatch({
				type: actions.SET_STATE,
				payload: {
					contentStatus: 'isMenu'
				}
			});
			return;
		}

		if (!isLog || !authorized) {
			dispatch({
				type: 'auth/LOGOUT_POS',
				history
			});
			return;
		}
	}, [isLog, authorized]);

	useEffect(() => {
		handleFocus();
		handleCancel();

		document.addEventListener('keydown', onKeyDownGlobal);
		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	useEffect(() => {
		if (contentStatus === 'isMenu') {
			handleFocus();
		}
	}, [contentStatus]);

	useEffect(() => {
		if (document.activeElement.id === 'scan-item') {
			if (searchResult.length !== 0 && searchResult.length === 1) {
				handleAddItem({ upcBarcode: searchResult[0]?.UPCbarcode, ItemCode: '' }, 'input');
			}
		}

		handleFocus();
	}, [searchResult, inputRef]);

	useEffect(() => {
		inputRef && inputRef.current !== null && inputRef.current.focus();
	}, [billNo, billDate, inputRef, state]);

	return (
		<div className="w-screen h-screen overflow-hidden relative pb-2.5">
			<div className="w-full h-full grid grid-cols-12 gap-3" style={{ gridTemplateRows: 'repeat(24, minmax(0, 1fr))' }}>
				<div className="col-span-12 row-span-3 bg-primary">
					<HeaderLayout />
				</div>
				<div className="col-span-10 row-span-5 pl-2.5">
					<div className="w-full h-full grid grid-cols-12 grid-rows-2 rounded-lg">
						<div className="row-start-1 col-span-6 row-span-1 rounded-lg bg-primary rounded-r-none">
							<div className="w-full h-full flex items-center justify-end">
								<h1 className="text-3xl text-white font-semibold text-right">TOTAL BELANJA</h1>
							</div>
						</div>
						<div className="row-start-2 col-span-6 relative">
							<Form form={form} layout="vertical">
								<div className="w-5 h-5 bg-primary absolute -top-4 -right-4 rounded-full" />
								<div className="w-full h-full grid grid-cols-7 gap-2 pt-2.5 pr-2.5">
									<div className="col-span-2">
										<div className="w-full h-16 bg-primary rounded-lg">
											<div className="w-full h-full flex items-center justify-center flex-col gap-1">
												<p className="text-center text-gray-200 text-xs">TOTAL QTY</p>
												<p className="text-center text-white text-lg font-bold">{getTotalQty()}</p>
											</div>
										</div>
									</div>
									<div className="col-span-5">
										<div className="w-full bg-white rounded-lg overflow-hidden" style={{ height: '68px' }}>
											<Form.Item name="search-item">
												<Input
													id="scan-item"
													value={state?.barcode}
													ref={inputRef}
													autoFocus
													className="w-full rounded-lg px-4 text-lg placeholder-gray-800 focus:border-2 focus:border-gray-500 custom-input"
													placeholder="Barcode / PLU Barang (ESC)"
													suffix={
														loading ? (
															<CgSpinner className="animate-spin text-xl text-gray-500" />
														) : (
															<BiSearch className="text-xl text-gray-500" />
														)
													}
													style={{ height: '68px' }}
													onPressEnter={(event) => {
														handleSearchItem(event.target.value);
													}}
													onChange={(event) => {
														setState((prev) => ({
															...prev,
															barcode: event.target.value
														}));
													}}
												/>
											</Form.Item>
										</div>
									</div>
								</div>
							</Form>
						</div>
						<div className="row-start-1 row-span-2 col-span-6 rounded-lg overflow-hidden bg-primary rounded-tl-none">
							<div className="w-full h-full flex items-center justify-end p-3">
								<h1 className="text-7xl text-white font-semibold text-right">{currencyFormat(getGrandTotal(), 0)}</h1>
							</div>
						</div>
					</div>
				</div>
				<div className="col-span-2 pr-2.5" style={{ gridRow: 'span 21 / span 21' }}>
					<ButtonActionOrder state={state} handleState={handleState} />
				</div>
				<div className="col-span-10 pl-2.5" style={{ gridRow: 'span 16 / span 16' }}>
					<TableOrderItem handleFocus={handleFocus} handleState={handleState} state={state} />
				</div>
			</div>

			<ModalPendingPayment
				visible={state?.modalPendingPayment}
				onCancel={handleCancel}
				handleSearchFocus={handleFocus}
				handleState={handleState}
			/>
			<ModalTableSearchItem open={state?.modalSearchItem} handleCancel={handleCancel} state={state} handleState={handleState} />
			<ModalConfirmDelete open={state?.modalConfirmDelete} handleCancel={handleCancel} state={state} handleState={handleState} />
			<ModalChangeQty open={state?.modalQty} handleCancel={handleCancel} state={state} handleState={handleState} />
		</div>
	);
}
