import { Form, notification } from 'antd';
import { useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import actions from '../../../redux/transaction/actions';

export default function useCartOrder() {
	const dispatch = useDispatch();
	const inputRef = useRef(null);
	const [form] = Form.useForm();

	const { searchResult = [], data = [] } = useSelector((state) => state?.transaction);

	const handleFocus = () => {
		if (inputRef && inputRef.current !== null) {
			inputRef.current.focus();
			return;
		}
	};

	const handleResetForm = () => {
		form.resetFields();
		handleFocus();

		dispatch({
			type: actions.SET_STATE,
			payload: {
				searchResult: []
			}
		});
	};

	const handleSearch = (query = '') => {
		if (query !== '') {
			dispatch({
				type: 'transaction/SEARCH_MASTER',
				payload: {
					query
				}
			});

			handleResetForm();
			return;
		}

		notification.warning({
			message: 'Perhatian!',
			description: 'Masukkan Kode Item / Kode Barcode terlebih dahulu.'
		});
	};

	const handleSelectItem = (selected = {}) => {
		handleAddItem({ ItemCode: selected, upcBarcode: '' }, 'select');
	};

	const handleAddItem = (item = {}, from = 'input') => {
		handleResetForm();

		const findData = searchResult.find((itm) => +itm.upcBarcode === +item.upcBarcode);

		if (from === 'input' && findData !== undefined) {
			if (item?.upcBarcode !== undefined && item.upcBarcode && item.upcBarcode !== '') {
				const findData = searchResult.find((itm) => +itm.upcBarcode === +item.upcBarcode);
				const anyData = data.filter((el) => +el.upcBarcode === +item.upcBarcode);

				//* JIKA DITEMUKAN BARANG DENGAN UPCBARCODE YANG SAMA +1 QTYNYA
				if (findData && anyData?.length > 0) {
					const restData = [];

					data.map((el) => {
						if (+el.upcBarcode === +item.upcBarcode) {
							restData.push({
								...el,
								qty: el.qty + 1,
								total: (+el.qty + 1) * el.price,
								amount: (+el.qty + 1) * el.price
							});
						} else {
							return restData.push(el);
						}
						return true;
					});

					dispatch({
						type: actions.SET_STATE,
						payload: {
							data: [...restData],
							contentStatus: 'isMenu'
						}
					});
				}

				// * DITEMUKAN BANRANG NAMUN TIDAK ADA DI LIST TRANSAKSI
				if (findData && anyData.length === 0) {
					dispatch({
						type: actions.SET_STATE,
						payload: {
							contentStatus: 'isMenu',
							data: [
								...data,
								{
									...findData,
									itemCode: data.length + 1,
									qty: 1,
									total: findData.price,
									amount: findData.price
								}
							]
						}
					});
				}
			}
		}

		if (from === 'select') {
			if (item.ItemCode && from === 'select') {
				const findData = searchResult.find((itm) => itm.ItemCode === item.ItemCode);
				const anyData = data.filter((el) => el.ItemCode === item.ItemCode);

				// * JIKA DITEMUKAN BARANG DENGAN UPCBARCODE YANG SAMA +1 QTYNYA
				if (findData && anyData.length > 0) {
					const restData = [];

					data.map((el) => {
						if (el.ItemCode === item.ItemCode) {
							restData.push({
								...el,
								qty: el.qty + 1,
								total: (+el.qty + 1) * el.price,
								amount: (+el.qty + 1) * el.price
							});
						} else {
							return restData.push(el);
						}
						return true;
					});

					dispatch({
						type: actions.SET_STATE,
						payload: {
							data: [...restData]
						}
					});
				}

				if (findData && anyData.length === 0) {
					dispatch({
						type: actions.SET_STATE,
						payload: {
							data: [
								...data,
								{
									...findData,
									itemCode: data.length + 1,
									qty: 1,
									total: findData.price,
									amount: findData.price
								}
							]
						}
					});
				}
			}
		}

		if (from === 'table') {
			const findData = searchResult.find((itm) => itm.ItemCode === item.ItemCode);
			const anyData = data.filter((el) => el.ItemCode === item.ItemCode);

			if (findData && anyData.length > 0) {
				const restData = [];

				data?.map((el) => {
					if (el.ItemCode === item.ItemCode) {
						restData.push({
							...el,
							qty: el.qty + 1,
							total: (+el.qty + 1) * el.price,
							amount: (+el.qty + 1) * el.price
						});
					} else {
						return restData.push(el);
					}

					return true;
				});

				dispatch({
					type: actions.SET_STATE,
					payload: {
						data: [...restData]
					}
				});
			}

			if (findData && anyData.length === 0) {
				dispatch({
					type: actions.SET_STATE,
					payload: {
						data: [
							...data,
							{
								...findData,
								itemCode: data.length + 1,
								qty: 1,
								total: findData.price,
								amount: findData.price
							}
						]
					}
				});
			}
		}

		handleFocus();
	};

	const handleDeleteItem = (itemCode = '') => {
		const filteredDataTrx = (data?.length !== 0 && data?.filter(({ ItemCode }) => ItemCode !== itemCode)) || [];

		dispatch({
			type: actions.SET_STATE,
			payload: {
				data: filteredDataTrx,
				selectedRowTransaction: filteredDataTrx[0]
			}
		});

		handleFocus();
	};

	return { inputRef, form, handleFocus, handleResetForm, handleSearch, handleSelectItem, handleAddItem, handleDeleteItem };
}
