import { Button } from 'antd';
import React, { useEffect } from 'react';

export default function ListItemButton({ hs = () => {} }) {
	const handleOpen = () => {
		hs('modalSearchItem', true);
	};

	const onKeyDown = (e) => {
		if (e.key === 'F2') {
			handleOpen();
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);
		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, []);

	return (
		<>
			<Button className="btn-page rounded-lg" onClick={handleOpen}>
				<kbd className="kbd">f2</kbd>
				<p>Daftar Barang</p>
			</Button>
		</>
	);
}
