import { Button, Col, Form, Input, Modal, Row } from 'antd';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import useCartOrder from '../hooks/useCartOrder';

export default function ModalConfirmDelete({ open, state, handleCancel, handleState = (state, val) => {} }) {
	const dispatch = useDispatch();
	const history = useHistory();

	const { handleDeleteItem } = useCartOrder();
	const { selectedRowTransaction = {} } = useSelector((state) => state?.transaction);
	const { isGrantedAccess = false, loading = false } = useSelector((state) => state?.auth);

	const isSelected = Object.keys(selectedRowTransaction).length !== 0;
	const modalRef = useRef(null);
	const emailRef = useRef();
	const passwordRef = useRef();
	const deleteRef = useRef(null);

	const [form] = Form.useForm();

	const onFinish = (values) => {
		if (isGrantedAccess) {
			handleDeleteItem(selectedRowTransaction?.ItemCode);
			handleCancel();
			return;
		}

		dispatch({
			type: 'auth/USER_ACCESS',
			payload: {
				email: values?.email,
				password: window.btoa(values?.password),
				funcId: '1031'
			},
			history
		});
	};

	useEffect(() => {
		dispatch({
			type: 'auth/SET_STATE',
			payload: {
				isGrantedAccess: false
			}
		});
	}, [open]);

	return (
		<Modal centered open={open} onCancel={handleCancel} title={<h1 className="text-lg">Hapus Barang</h1>} footer={false}>
			<div ref={modalRef} tabIndex={1}>
				{isGrantedAccess ? (
					<div className="w-full">
						<div className="w-full h-auto p-5 py-7 bg-gray-100 rounded-lg">
							<h1 className="text-base text-center ">
								<b>Konfirmasi Berhasil.</b> Anda dapat menghapus item/barang yang terpilih.
							</h1>

							<Button
								danger
								autoFocus
								ref={deleteRef}
								className="btn-page w-max px-7 mx-auto mt-5"
								onClick={() => onFinish(form.getFieldsValue())}
								onKeyDown={(event) => {
									if (document.activeElement.id !== 'input-qty' && isSelected) {
										if (event.ctrlKey && event.key === 'Enter') {
											onFinish(form.getFieldsValue());
										}
									}
								}}>
								Hapus Item
							</Button>
						</div>
					</div>
				) : (
					<div className="w-full">
						<div className="mb-5">
							<p className="text-base text-gray-800">
								Apakah anda yakin akan menghapus item/barang <b>{selectedRowTransaction?.ItemName}</b>. Mohon konfirmasi terlebih
								dahulu.
							</p>
						</div>

						<Form
							form={form}
							name="login"
							layout="vertical"
							initialValues={{
								remember: true
							}}
							onFinish={onFinish}
							onFinishFailed={console.log}
							autoComplete="off"
							style={{ maxWidth: 'none', width: '100%' }}>
							<Row wrap gutter={[8, 0]}>
								<Col span={24}>
									<Form.Item
										label="Email"
										name="email"
										rules={[
											{
												required: true,
												message: 'Wajib Diisi!'
											}
										]}>
										<Input
											ref={emailRef}
											type="email"
											autoFocus
											autoComplete="false"
											placeholder="usertest@domain.com"
											className="w-full h-12 rounded-md"
										/>
									</Form.Item>
								</Col>
								<Col span={24}>
									<Form.Item
										label="Password"
										name="password"
										rules={[
											{
												required: true,
												message: 'Wajib Diisi!'
											}
										]}>
										<Input.Password
											ref={passwordRef}
											type="password"
											autoComplete="false"
											placeholder="********"
											className="w-full h-12 rounded-md"
										/>
									</Form.Item>
								</Col>

								<Col span={24}>
									<div className="w-full flex items-center justify-start gap-2 mt-3">
										<Form.Item>
											<Button
												disable={loading}
												loading={loading}
												type="primary"
												htmlType="submit"
												className="w-full btn-page px-7">
												Validasi
											</Button>
										</Form.Item>
										<Form.Item>
											<Button
												type="default"
												htmlType="button"
												className="w-full h-14 flex items-center justify-center gap-2 rounded-md px-7"
												onClick={handleCancel}>
												Kembali
											</Button>
										</Form.Item>
									</div>
								</Col>
							</Row>
						</Form>
					</div>
				)}
			</div>
		</Modal>
	);
}
