import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import actions from '../../../redux/transaction/actions';
import useCartOrder from '../hooks/useCartOrder';

export default function NewOrderButton(props) {
	const dispatch = useDispatch();
	const app = JSON.parse(localStorage.getItem('app') || '{}');
	const { cashier = {} } = app;
	const isOpeningShift = cashier?.isOpening || false;

	const { hs = null } = props;
	const { loading = false } = useSelector((state) => state?.transaction);
	const { handleFocus } = useCartOrder();

	const handleNewOrder = () => {
		dispatch({
			type: actions.SET_STATE,
			payload: {
				transactionList: [],
				data: []
			}
		});

		dispatch({
			type: actions.TRANSACTION_BILL_NO
		});

		hs('data', []);
		hs('t', 0);
		hs('total', 0);
		hs('selectedData', []);
		hs('isSelected', []);

		handleFocus();
	}; 

	const onKeyDownGlobal = (e) => {
		if (isOpeningShift) {
			if (e.key === 'F1') {
				e.target.blur();
				if (document.activeElement.id !== 'barcode') {
					handleNewOrder();
					return;
				}

				return e;
			}
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);
		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	return (
		<Button loading={loading} disabled={loading || !isOpeningShift} className="btn-page" onClick={handleNewOrder}>
			<kbd className="kbd">f1</kbd>
			<p>order baru</p>
		</Button>
	);
}
