import { Image } from 'antd';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

export default function HeaderLayout() {
	const { user = {}, cashier = {} } = JSON.parse(localStorage.getItem('app') || '{}');

	const { billDate = '', billNo = '', loading = false } = useSelector((state) => state?.transaction);

	const [state, setState] = useState({
		modalCashBox: false,
		currentTime: moment()
	});

	useEffect(() => {
		const timerId = setInterval(() => {
			setState((prev) => ({
				...prev,
				currentTime: moment()
			}));
		}, 1000);

		return () => {
			clearInterval(timerId);
		};
	}, []);

	return (
		<div className="w-full h-full flex items-center justify-center">
			<div className="w-full flex items-center justify-between px-5">
				<div className="w-2/5 flex items-center gap-2">
					<Image src="/logo-3.png" className="w-28 object-cover" preview={false} />
					<h1 className="text-xl text-white font-semibold leading-none">Scap Point of Sales</h1>
				</div>
				<div className="w-full h-max">
					<div className="w-full flex items-center justify-end gap-5">
						<div className="border-r-2 border-gray-300 pr-5">
							<p className="text-right text-gray-200 text-xs font-semibold uppercase">buka</p>
							<h1 className="text-right text-white text-md uppercase font-bold">
								{loading ? '...' : moment(billDate).format('DD MMMM YYYY')}
							</h1>
						</div>
						<div className="border-r-2 border-gray-300 pr-5">
							<p className="text-right text-gray-200 text-xs font-semibold uppercase">TANGGAL</p>
							<h1 className="text-right text-white text-md uppercase font-bold">{moment().format('DD MMMM YYYY')}</h1>
						</div>
						<div className="border-r-2 border-gray-300 pr-5">
							<p className="text-right text-gray-200 text-xs font-semibold uppercase">WAKTU</p>
							<h1 className="text-right text-white text-md uppercase font-bold">{state.currentTime.format('HH:mm:ss')}</h1>
						</div>
						<div className="border-r-2 border-gray-300 pr-5">
							<p className="text-right text-gray-200 text-xs font-semibold uppercase">NO. TRANSAKSI</p>
							<h1 className="text-right text-white text-md uppercase font-bold">{loading ? '...' : billNo || '-'}</h1>
						</div>
						<div className="">
							<p className="text-right text-gray-200 text-xs font-semibold uppercase">
								{user?.role || 'nama kasir'} | ID 0{cashier?.shiftId || '0'}
							</p>
							<h1 className="text-right text-white text-md uppercase font-bold">{user?.username || 'guest'}</h1>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
