import { LoadingOutlined } from '@ant-design/icons';
import { Form, Input, Modal } from 'antd';
import moment from 'moment';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { currencyFormat } from '../../../utils/helper/utilities';
import useCartOrder from '../hooks/useCartOrder';

export default function ModalPendingPayment(props) {
	const dispatch = useDispatch();
	const history = useHistory();

	const { visible, onCancel } = props;
	const { transactionHolds = [], loading = false } = useSelector((state) => state?.transaction);
	const { inputRef: searchItemRef } = useCartOrder();

	const refTable = useRef(null);
	const refInput = useRef(null);
	const modalRef = useRef(null);
	const [state, setState] = useState({
		search: '',
		t: 0,
		isSelected: false,
		dataTable: [],
		selectedData: {}
	});

	const handleDetailHoldTrx = (billNo) => {
		dispatch({
			type: 'transaction/HOLD_DETAIL',
			payload: {
				billNo
			},
			history,
			onClose: handleCancel,
			onState: props?.handleState
		});
	};

	const onKeyPress = (e) => {
		const allowedKeys = ['ArrowUp', 'ArrowDown', 'Enter', 'Escape'];
		const { t, dataTable } = state;

		searchItemRef && searchItemRef.current !== null && searchItemRef.current.blur();
		if (e.key === 'f') {
			focusInput();
		}

		if (allowedKeys.includes(e.key) && visible) {
			refInput && refInput.current.blur();
			searchItemRef && searchItemRef.current !== null && searchItemRef.current.blur();

			const table = refTable && refTable.current;

			if (e.key === 'ArrowUp' && dataTable.length && dataTable.length > 0 && table !== null) {
				refInput.current.blur();

				if (t >= 2) {
					setState({
						...state,
						t: t - 1,
						isSelected: true,
						selectedData: dataTable[t - 1]
					});
				}

				if (t === 1) {
					setState({
						...state,
						t: 0,
						isSelected: true,
						selectedData: dataTable[0]
					});
				}

				const tbody = table.lastChild || null;
				const tr = (tbody && tbody !== null && tbody.children) || [];

				if (tr[t - 1]) {
					return tr[t - 1].focus();
				}
			}

			if (e.key === 'ArrowDown' && dataTable.length && dataTable.length > 0 && table !== null) {
				refInput.current.blur();

				const tbody = table.lastChild || null;
				const tr = (tbody && tbody !== null && tbody.children) || [];

				if (t === dataTable.length) {
					setState({
						...state,
						t: dataTable.length,
						isSelected: true,
						selectedData: dataTable[dataTable.length]
					});

					if (tr[t]) {
						return tr[t].focus();
					}
				}

				if (t < dataTable.length) {
					setState({
						...state,
						t: t + 1,
						isSelected: true,
						selectedData: dataTable[t + 1]
					});

					if (tr[t + 1]) {
						return tr[t + 1].focus();
					}
				}
			}

			if (e.key === 'Enter' && state?.isSelected) {
				const isNotEmptyTrx = Object.keys(state?.selectedData || {}).length !== 0;

				focusInput();
				if (isNotEmptyTrx) {
					handleDetailHoldTrx(state?.selectedData?.billNo);
					return;
				}

				return null;
			}

			if (e.key === 'Escape') {
				handleCancel();
			}
		}
	};

	const handleDataTable = (query = '') => {
		if (query && query !== '') {
			const filtered = transactionHolds
				.filter(
					(el) => el.billNo.toLowerCase().indexOf(query.toLowerCase()) >= 0 || el.billNo.toLowerCase().indexOf(query.toLowerCase()) >= 0
				)
				.map((val, i) => ({ ...val, key: i + 1 }));

			if (filtered.length !== 0) {
				setState({
					...state,
					dataTable: filtered,
					t: 0,
					selectedData: {},
					isSelected: false
				});

				return;
			}

			return;
		}

		const defaultTrx = transactionHolds?.map((val, i) => ({ ...val, key: i + 1 }))?.slice(0, 30);
		setState({
			...state,
			dataTable: defaultTrx,
			t: 0,
			selectedData: {},
			isSelected: false
		});
	};

	const handleCancel = () => {
		setState({
			...state,
			isSelected: false,
			selectedData: {}
		});

		onCancel();
	};

	const focusInput = () => {
		if (refInput && refInput.current !== null) {
			refInput.current.focus();
		}
	};

	useEffect(() => {
		focusInput();
		setState({
			...state,
			t: 0
		});

		return () => {
			setState(() => {
				return;
			});
		};
	}, []);

	useEffect(() => {
		const data = transactionHolds.map((val, i) => ({ ...val, key: i + 1 }));
		setState({
			...state,
			dataTable: data?.slice(0, 30)
		});

		focusInput();
	}, [transactionHolds]);

	useEffect(() => {
		const table = refTable && refTable.current !== null && refTable.current;

		if (table) {
			focusInput();
			document.addEventListener('keydown', onKeyPress);
		}

		return () => {
			document.removeEventListener('keydown', onKeyPress);
		};
	}, [refTable, state]);

	return (
		<Modal open={visible} onCancel={handleCancel} centered width={850} footer={null}>
			<div ref={modalRef} onKeyDown={onKeyPress} className="w-full h-auto">
				<div className="w-full flex flex-row items-center justify-center gap-2 text-center  mb-4">
					<h1 className="text-xl font-semibold text-center">Transaksi Pending</h1>
					{loading && <LoadingOutlined spin className="text-lg" />}
				</div>
				<div className="w-full">
					<Form className="w-full">
						<Form.Item className="w-full">
							<Input
								ref={refInput}
								autoFocus
								placeholder="Masukkan Nomor Bill( F )"
								className="text-center flex items-center justify-center h-20 w-full rounded-md bg-gray-200 text-lg text-gray-800"
								onChange={(e) => handleDataTable(e.target.value)}
							/>
						</Form.Item>
					</Form>
				</div>
				<div className="w-full">
					<table ref={refTable} id="table" className="table-customer w-full relative">
						<thead className="w-full">
							<tr key="id-head" className="w-full bg-primary focus:bg-gray-700 h-14">
								<th className="w-max text-center text-white px-5">No</th>
								<th className="w-max text-left text-white px-5">Tanggal Transaksi</th>
								<th className="w-max text-left text-white px-5">Bill No</th>
								<th className="w-max text-left text-white px-5">Subtotal</th>
							</tr>
						</thead>
						<tbody>
							{state?.dataTable?.map((item, index) => {
								const { billNo = '', billTime = '', subTotal = '', key = '' } = item || {};
								return (
									<tr
										id={index}
										tabIndex={index}
										className={`tr-data h-14 ${index % 2 === 0 ? 'bg-white' : 'bg-gray-100'} ${
											state?.selectedDataTrx?.billNo === item?.billNo ? 'bg-yellow-200' : 'bg-white'
										} focus:bg-yellow-200 hover:bg-yellow-200 focus:font-bold transition-all ease-in-out cursor-pointer`}
										key={key}
										onClick={() => {
											setState({
												...state,
												isSelected: true,
												selectedDataTrx: item
											});

											handleDetailHoldTrx(billNo);
										}}>
										<td className="px-5 text-center">{index + 1}</td>
										<td className="px-5 text-left">{moment(billTime).format('DD MMM YYYY hh:mm:ss')}</td>
										<td className="px-5 h-14 text-left flex items-center flex-row gap-2 content-center">
											{billNo}
											{loading && <LoadingOutlined spin className="text-lg" />}
										</td>
										<td className="px-5 text-left">Rp {currencyFormat(subTotal, 0)}</td>
									</tr>
								);
							})}
						</tbody>
					</table>
				</div>
			</div>
		</Modal>
	);
}
