import { Col, Input, Modal, notification, Row } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import actions from '../../../redux/transaction/actions';
import { currencyFormat } from '../../../utils/helper/utilities';

export default function ModalChangeQty({ open, handleState, handleCancel }) {
	const dispatch = useDispatch();

	const { selectedRowTransaction = {}, data = [] } = useSelector((state) => state?.transaction);
	const { qty: oldQty, ItemCode = '', price = 0, upcBarcode = '', itemName = '' } = selectedRowTransaction;

	const inputRef = useRef(null);
	const modalRef = useRef(null);

	const [state, setState] = useState({
		qty: oldQty,
		inputChanged: false
	});

	const handleFocus = () => {
		const input = inputRef && inputRef.current !== null;

		if (input) {
			inputRef.current.focus();
			inputRef.current.select();
		}
	};

	const handleChange = (value) => {
		setState((prev) => ({
			...prev,
			qty: +value
		}));
	};

	const handleEnter = () => {
		const { qty = 0 } = state;
		handleFocus();

		if (+qty === 0 || +qty <= 0) {
			notification.error({
				message: 'Perhatian!',
				description: 'Kuantitas tidak boleh kosong'
			});

			handleFocus();
			return;
		}

		if (qty === '' || qty === oldQty) {
			handleChangeQty(+oldQty);
			return;
		}

		handleChangeQty(qty);
	};

	const handleChangeQty = (qty = oldQty) => {
		const anyData = data?.length !== 0 && data?.filter((e) => e.ItemCode === ItemCode);

		if (anyData && qty <= 0) {
			notification.info({
				message: 'Perhatian!',
				description: 'QTY Tidak boleh Nol (0)'
			});
			return;
		}

		if (anyData && qty < oldQty) {
			handleCancel();
			notification.error({
				message: 'Perhatian!',
				description: 'Anda tidak dapat mengubah quantity kurang dari quantity awal!.'
			});
			notification.error({
				message: 'Perhatian!',
				description: 'Fitur ini sedang dalam tahap pengembangan.'
			});
			return;
		}

		if (anyData && qty >= 1) {
			const restData = [];

			data?.map((el) => {
				if (el.ItemCode === ItemCode) {
					restData.push({
						...el,
						qty: +qty,
						total: +qty * +price,
						amount: +qty * +price
					});
				} else {
					return restData.push(el);
				}

				return true;
			});

			dispatch({
				type: actions.SET_STATE,
				payload: {
					data: [...restData]
				}
			});
		}

		handleCancel();
		setState((prev) => ({
			...prev,
			qty: 1
		}));
	};

	const handleKeyDownGlobal = (e) => {
		const allowedKeys = ['ArrowUp', 'ArrowDown'];
		const key = e.key;

		if (allowedKeys.includes(key)) {
			e.preventDefault();

			if (key === 'ArrowUp') {
				console.log(inputRef);
				handleFocus();
			} else if (key === 'ArrowDown') {
				console.log(document.activeElement);
			}
		}
	};

	useEffect(() => {
		handleFocus();
	}, []);

	useEffect(() => {
		handleFocus();
	}, [selectedRowTransaction]);

	useEffect(() => {
		if (open) {
			handleFocus();
		}
	}, [open]);

	useEffect(() => {
		handleFocus();
	}, [inputRef]);

	useEffect(() => {
		const input = inputRef && inputRef.current !== null && inputRef.current;

		if (input) {
			handleFocus();
			document.addEventListener('keydown', handleKeyDownGlobal);
		}

		return () => {
			document.removeEventListener('keydown', handleKeyDownGlobal);
		};
	}, [modalRef, inputRef, open]);

	return (
		<Modal centered open={open} onOk={handleEnter} onCancel={handleCancel} title={<p className="text-lg">Ubah Quantity</p>}>
			<Row ref={modalRef} tabIndex={1} onKeyDown={handleKeyDownGlobal} wrap gutter={[8, 8]}>
				<Col span={24}>
					<Row>
						<Col span={10}>Barcode / PLU</Col>
						<Col span={1}>:</Col>
						<Col span={11}>{`${upcBarcode} / ${ItemCode}`}</Col>
					</Row>
				</Col>
				<Col span={24}>
					<Row>
						<Col span={10}>Nama Barang</Col>
						<Col span={1}>:</Col>
						<Col span={11}>{itemName}</Col>
					</Row>
				</Col>
				<Col span={24}>
					<Row>
						<Col span={10}>Harga Satuan</Col>
						<Col span={1}>:</Col>
						<Col span={11}>{currencyFormat(price, 0)}</Col>
					</Row>
				</Col>
				<Col span={24}>
					<Row>
						<Col span={10}>Qty</Col>
						<Col span={1}>:</Col>
						<Col span={11}>{oldQty}</Col>
					</Row>
				</Col>
				<Col span={24}>
					<Input
						autoFocus
						id="input-qty"
						value={state?.qty}
						defaultValue={oldQty}
						disabled={state.inputChanged}
						ref={inputRef}
						onChange={(event) => handleChange(event.target.value)}
						onPressEnter={() => {
							handleEnter();
						}}
						className="w-full h-14 rounded-lg mt-5 bg-gray-200"
						placeholder="MASUKKAN JUMLAH QTY"
						onKeyDown={handleKeyDownGlobal}
					/>
					<p className="text-base text-red-600 mt-2">
						*Gunakan <b>&#8679;</b> untuk memasukkan jumlah qty
					</p>
				</Col>
			</Row>
		</Modal>
	);
}
