import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';

export default function DeleteItemButton({ hs = (state, val) => {} }) {
	const { selectedRowTransaction = {} } = useSelector((state) => state?.transaction);
	const isSelected = Object.keys(selectedRowTransaction).length !== 0;

	const handleOpenModal = () => {
		hs('modalConfirmDelete', true);
	};

	const handleKeyDownGlobal = (e) => {
		if (document.activeElement.id !== 'input-qty' && isSelected) {
			if (e.ctrlKey && e.key === 'Enter') {
				e.preventDefault();
				e.target.blur();

				handleOpenModal();
			}

			return null;
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', handleKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', handleKeyDownGlobal);
		};
	}, []);

	return (
		<Button className="btn-page rounded-lg" onClick={handleOpenModal}>
			<kbd className="kbd">ctrl+enter</kbd>
			<p>hapus item</p>
		</Button>
	);
}
