import { Form, Input, Modal, notification } from 'antd';
import React, { useEffect, useRef } from 'react';
import { BiSearch } from 'react-icons/bi';
import { CgSpinner } from 'react-icons/cg';
import { useDispatch, useSelector } from 'react-redux';
import useCartOrder from '../hooks/useCartOrder';
import TableSearchItem from './TableSearchItem';

export default function ModalTableSearchItem({ open = false, state, handleState, handleCancel }) {
	const dispatch = useDispatch();
	const inputRef = useRef(null);
	const modalRef = useRef(null);

	const { loading = false } = useSelector((state) => state?.transaction);
	const { inputRef: searchInputRef, form } = useCartOrder();

	const handleSearch = (query = '') => {
		if (query !== '') {
			dispatch({
				type: 'transaction/SEARCH_MASTER',
				payload: {
					query
				}
			});

			form.resetFields();
			focusInput();
			return;
		}

		notification.warning({
			message: 'Perhatian!',
			description: 'Masukkan Kode Item / Kode Barcode terlebih dahulu.'
		});
	};

	const focusInput = () => {
		searchInputRef && searchInputRef.current !== null && searchInputRef.current.blur();
		if (inputRef && inputRef.current !== null) {
			inputRef.current.focus();
			inputRef.current.select();
		}
	};

	const onKeyDown = (e) => {
		searchInputRef && searchInputRef.current !== null && searchInputRef.current.blur();

		if (e.key === 'F3') {
			e.preventDefault();
			focusInput();
		}
	};

	useEffect(() => {
		focusInput();
		form.resetFields();
	}, []);

	useEffect(() => {
		const modal = modalRef && modalRef.current !== null && modalRef.current;

		if (modal) {
			focusInput();
			document.addEventListener('keydown', onKeyDown);
		}

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, [modalRef, open]);

	return (
		<Modal open={open} onCancel={handleCancel} centered width={1000} title="Pencarian Barang">
			<div ref={modalRef} onKeyDown={onKeyDown} className="w-full h-auto" id="modal-container">
				<div>
					<Form form={form}>
						<Form.Item name="search">
							<Input
								autoFocus
								ref={inputRef}
								// value={state?.barcode}
								type="text"
								className="w-full h-14 rounded-lg border-2 border-gray-400"
								placeholder="MASUKKAN UPL / BARCODE ITEM (F3)"
								// onChange={(event) => {
								// 	handleState('barcode', event.target.value);
								// }}
								onPressEnter={(event) => {
									handleSearch(event.target.value);
									form.resetFields();
								}}
								suffix={
									loading ? (
										<CgSpinner className="animate-spin text-xl text-gray-500" />
									) : (
										<BiSearch className="text-xl text-gray-500" />
									)
								}
							/>
						</Form.Item>
					</Form>
				</div>
				<div className="w-full mt-4">
					<TableSearchItem modalRef={modalRef} handleCancel={handleCancel} />
				</div>
			</div>
		</Modal>
	);
}
