import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function SavePendingButton({ isDisabled = false }) {
	const dispatch = useDispatch();
	const history = useHistory();

	const { data = [], loading = false, billNo = '', billDate = '' } = useSelector((state) => state?.transaction);
	const { property = {}, cashier = {} } = JSON.parse(localStorage.getItem('app') || '{}');

	const disabled = isDisabled || data?.length === 0 || loading || isDisabled;

	const handlePendingPayment = () => {
		const subTotal = data.reduce((a, { total }) => +a + +total, 0);

		const header = [
			{
				billNo,
				billDate,
				custCode: '',
				custName: '',
				discPrc: '0',
				discValue: '0',
				taxPrc: '0',
				taxValue: '0',
				subTotal: subTotal,
				grandTotal: subTotal,
				shiftID: cashier?.shiftId,
				outletCode: property?.outletCode
			}
		];

		const payload = {
			header,
			details: data?.map((el, i) => ({ ...el, billNo, ItemNo: i + 1 }))
		};

		dispatch({
			type: 'transaction/HOLD',
			payload,
			history
		});
	};

	const onKeyDownGlobal = (e) => {
		if (!disabled) {
			if (e.key === 'F6') {
				e.preventDefault();
				handlePendingPayment();
			}
		}
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDownGlobal);

		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, [data]);

	return (
		<Button loading={loading} disabled={disabled} className="btn-page" onClick={handlePendingPayment}>
			<kbd className="kbd">f6</kbd>
			<p>simpan pending</p>
		</Button>
	);
}
