import { Button } from 'antd';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/transaction/actions';

export default function PaymentButton() {
	const dispatch = useDispatch();
	const history = useHistory();

	const { tenderMedia = [], loading = false, data = [] } = useSelector((state) => state?.transaction);

	const handleOpenPayment = () => {
		dispatch({
			type: 'auth/property'
		});

		dispatch({
			type: actions.SET_STATE,
			payload: {
				contentStatus: 'isPayment'
			}
		});

		history.push('/pos/payment');
	};

	const getTenderMedia = () => {
		dispatch({
			type: 'transaction/TENDER_MEDIA'
		});
	};

	const onKeyDownGlobal = (e) => {
		if (e.key === 'F10') {
			e.preventDefault();
			handleOpenPayment();
		}
	};

	useEffect(() => {
		getTenderMedia();

		document.addEventListener('keydown', onKeyDownGlobal);
		return () => {
			document.removeEventListener('keydown', onKeyDownGlobal);
		};
	}, []);

	return (
		<>
			<Button
				disabled={loading || tenderMedia.length === 0 || data?.length === 0}
				loading={loading}
				className="btn-page-2"
				onClick={handleOpenPayment}>
				<kbd className="kbd">f10</kbd>
				<p>pembayaran</p>
			</Button>
		</>
	);
}
